<template>
  <app-loader v-if="loading"></app-loader>
  <app-page title="Ariza" v-else-if="request" back>
    <p><strong>FISH</strong>:{{request.fish}}</p>
    <p><strong>TELEFON</strong>:{{request.phone}}</p>
    <p><strong>STATUS</strong>: <app-status :type="request.status"></app-status></p>
    <p><strong>SUMMA</strong>:{{currency(request.sum)}}</p>
    <div class="form-control">
      <label for="status">Statusni Ozgartirish</label>
      <select id="status" v-model="status">
        <option value="done">Bajarildi</option>
        <option value="cancelled">Qaytarildi</option>
        <option value="active">Tasdiqlanmoqda</option>
        <option value="sending">Yuborilmoqda</option>
      </select>
    </div>
    <button class="btn danger" @click="remove">Ochirish</button>
    <button class="btn" @click="update" v-if="hasCHanges">Ozgartirish</button>
  </app-page>
  <h3 v-else class="text-center text-white">Bunday ID = {{$route.params.id}} lik Ariza topilmadi</h3>
</template>
<script>
import {ref,onMounted,computed} from 'vue'
import {useStore} from 'vuex'
import {useRoute,useRouter} from 'vue-router'
import AppPage from '../components/ui/AppPage';
import AppStatus from "../components/ui/AppStatus";
import AppLoader from "../components/ui/AppLoader";
import {currency} from "../utils/Currency";
export default {
  setup(){
    const route = useRoute()
    const router = useRouter()
    const store = useStore()
    const loading = ref(true)
    const request = ref({})
    const status = ref()


    const hasCHanges = computed(()=> request.value.status !== status.value)

    const remove = async() => {
      await store.dispatch('request/remove', route.params.id)
      router.push('/')
    }
    const update = async() => {
      const data = {...request.value, status:status.value, id:route.params.id}
      await store.dispatch('request/update', data)
      request.value.status = status.value
    }

    onMounted(async ()=> {
      loading.value = true
      request.value = await store.dispatch('request/loadById',route.params.id)
      status.value = request.value.status
      loading.value = false
    })
    // console.log(route.params.id)


    return {
      request,
      currency,
      loading,
      remove,
      update,
      status,
      hasCHanges
    }
  },


  components:{AppPage,AppStatus,AppLoader}
}
</script>


